////
/// Fonts
/// @group base
////


@each $style in $font-families-styles {
    @font-face {
        font-family: "#{nth($style, 1)}";
        font-style: #{nth($style, 3)};
        font-weight: #{nth($style, 4)};
        font-display: $font-display;
        src: local("#{nth($style, 1)} #{nth($style, 2)}"),
             url("/fonts/#{nth($style, 5)}.woff2") format("woff2"),
             url("/fonts/#{nth($style, 5)}.woff") format("woff"),
             url("/fonts/#{nth($style, 5)}.ttf") format("truetype");
        unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
    }
}
